body {
  background-color: black;
  color: rgb(54, 230, 48);
}

nav {
  padding: 2% 3% 2% 3%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Space Mono', monospace;
}

nav div {
  display: flex;
  width: 270px;
}

.datos {
  padding-left: 7%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.datos div:first-child {
  font-weight: bold;
}

.redes {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

i {
  color: rgb(54, 230, 48);
}

.fotoperfil {
  border: 3px solid rgb(54, 230, 48);
  border-radius: 10px;
  height: 180px;
}